"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { supabase } from "@/lib/supabase";
import DashboardLayout from "@/components/dashboard/DashboardLayout";

interface Application {
  id: string;
  company_name: string;
  email: string;
  founder_name: string;
  status: string;
  reviewer_id: string | null;
  reviewers?: Array<{
    id: string;
    full_name: string | null;
  }>;
  reviewer?: {
    id: string;
    full_name: string | null;
  };
  created_at: string;
}

export default function DashboardPage() {
  const router = useRouter();
  const [user, setUser] = useState<any>(null);
  const [applications, setApplications] = useState<Application[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [filterStatus, setFilterStatus] = useState<string>("all");

  const fetchApplications = async () => {
    try {
      const backendUrl = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000';
      const params = filterStatus !== 'all' ? `?status=${filterStatus}` : '';
      const response = await fetch(`${backendUrl}/api/applications${params}`);
      
      if (response.ok) {
        const data = await response.json();
        console.log("Fetched applications:", data);
        setApplications(data.applications || []);
      } else {
        const errorData = await response.json().catch(() => ({}));
        console.error("Error fetching applications:", response.status, errorData);
      }
    } catch (error) {
      console.error("Error fetching applications:", error);
    }
  };

  useEffect(() => {
    checkUser();
  }, []);

  useEffect(() => {
    if (user) {
      fetchApplications();
    }
  }, [filterStatus, user]);

  const checkUser = async () => {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      
      if (!user) {
        router.push('/login');
        return;
      }

      // Check user role
      const { data: profile } = await supabase
        .from('user_profiles')
        .select('role')
        .eq('id', user.id)
        .single();

      if (!profile || (profile.role !== 'manager' && profile.role !== 'reviewer')) {
        router.push('/login');
        return;
      }

      setUser({ ...user, role: profile.role });
      fetchApplications();
    } catch (error) {
      console.error("Error checking user:", error);
      router.push('/login');
    } finally {
      setIsLoading(false);
    }
  };

  // Don't render anything if DashboardLayout handles auth
  if (isLoading) {
    return null;
  }


  const getStatusColor = (status: string) => {
    const colors: Record<string, string> = {
      pending: "bg-yellow-100 text-yellow-800 dark:bg-yellow-900/20 dark:text-yellow-200",
      under_review: "bg-blue-100 text-blue-800 dark:bg-blue-900/20 dark:text-blue-200",
      approved: "bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-200",
      rejected: "bg-red-100 text-red-800 dark:bg-red-900/20 dark:text-red-200",
      withdrawn: "bg-gray-100 text-gray-800 dark:bg-gray-900/20 dark:text-gray-200",
    };
    return colors[status] || "bg-gray-100 text-gray-800";
  };

  return (
    <DashboardLayout>
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-6">
          <h2 className="text-2xl font-bold mb-4 text-black dark:text-zinc-50">
            Startup Applications
          </h2>
          
          <div className="flex gap-2 mb-4">
            <button
              onClick={() => setFilterStatus("all")}
              className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
                filterStatus === "all"
                  ? "bg-black text-white dark:bg-zinc-50 dark:text-black"
                  : "bg-white dark:bg-zinc-900 border border-zinc-300 dark:border-zinc-700"
              }`}
            >
              All
            </button>
            <button
              onClick={() => setFilterStatus("pending")}
              className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
                filterStatus === "pending"
                  ? "bg-black text-white dark:bg-zinc-50 dark:text-black"
                  : "bg-white dark:bg-zinc-900 border border-zinc-300 dark:border-zinc-700"
              }`}
            >
              Pending
            </button>
            <button
              onClick={() => setFilterStatus("under_review")}
              className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
                filterStatus === "under_review"
                  ? "bg-black text-white dark:bg-zinc-50 dark:text-black"
                  : "bg-white dark:bg-zinc-900 border border-zinc-300 dark:border-zinc-700"
              }`}
            >
              Under Review
            </button>
            <button
              onClick={() => setFilterStatus("approved")}
              className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
                filterStatus === "approved"
                  ? "bg-black text-white dark:bg-zinc-50 dark:text-black"
                  : "bg-white dark:bg-zinc-900 border border-zinc-300 dark:border-zinc-700"
              }`}
            >
              Approved
            </button>
            <button
              onClick={() => setFilterStatus("rejected")}
              className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
                filterStatus === "rejected"
                  ? "bg-black text-white dark:bg-zinc-50 dark:text-black"
                  : "bg-white dark:bg-zinc-900 border border-zinc-300 dark:border-zinc-700"
              }`}
            >
              Rejected
            </button>
          </div>
        </div>

        <div className="bg-white dark:bg-zinc-900 rounded-lg shadow-sm border border-zinc-200 dark:border-zinc-800 overflow-hidden">
          {applications.length === 0 ? (
            <div className="p-8 text-center text-zinc-600 dark:text-zinc-400">
              No applications found.
            </div>
          ) : (
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead className="bg-zinc-50 dark:bg-zinc-800">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-zinc-500 dark:text-zinc-400 uppercase tracking-wider">
                      Company
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-zinc-500 dark:text-zinc-400 uppercase tracking-wider">
                      Founder
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-zinc-500 dark:text-zinc-400 uppercase tracking-wider">
                      Email
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-zinc-500 dark:text-zinc-400 uppercase tracking-wider">
                      Status
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-zinc-500 dark:text-zinc-400 uppercase tracking-wider">
                      Reviewer
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-zinc-500 dark:text-zinc-400 uppercase tracking-wider">
                      Submitted
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-zinc-500 dark:text-zinc-400 uppercase tracking-wider">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-zinc-200 dark:divide-zinc-800">
                  {applications.map((app) => (
                    <tr
                      key={app.id}
                      onClick={() => router.push(`/dashboard/applications/${app.id}`)}
                      className="hover:bg-zinc-50 dark:hover:bg-zinc-800 cursor-pointer transition-colors"
                    >
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm font-medium text-black dark:text-zinc-50">
                          {app.company_name}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-zinc-600 dark:text-zinc-400">
                          {app.founder_name}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-zinc-600 dark:text-zinc-400">
                          {app.email}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span
                          className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(
                            app.status
                          )}`}
                        >
                          {app.status.replace("_", " ")}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-zinc-600 dark:text-zinc-400">
                          {app.reviewers && app.reviewers.length > 0 ? (
                            <div className="flex flex-wrap gap-1">
                              {app.reviewers.slice(0, 2).map((reviewer) => (
                                <span
                                  key={reviewer.id}
                                  className="inline-block px-2 py-0.5 text-xs bg-blue-100 dark:bg-blue-900/20 text-blue-800 dark:text-blue-200 rounded"
                                >
                                  {reviewer.full_name || "Unknown"}
                                </span>
                              ))}
                              {app.reviewers.length > 2 && (
                                <span className="text-xs text-zinc-500">
                                  +{app.reviewers.length - 2} more
                                </span>
                              )}
                            </div>
                          ) : app.reviewer?.full_name ? (
                            app.reviewer.full_name
                          ) : (
                            <span className="text-zinc-400 dark:text-zinc-600">Not assigned</span>
                          )}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-zinc-600 dark:text-zinc-400">
                        {new Date(app.created_at).toLocaleDateString()}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm">
                        <Link
                          href={`/dashboard/applications/${app.id}`}
                          className="text-black dark:text-zinc-50 hover:underline"
                          onClick={(e) => e.stopPropagation()}
                        >
                          View â†’
                        </Link>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>
    </DashboardLayout>
  );
}
